@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
.Rules{font-size:14px}

.GameParamForm{display:flex;flex-direction:column}.GameParamForm__start-button{align-self:center;width:200px;padding:20px;border-radius:20px;font-size:24px;background-color:beige}

.Board{display:grid;grid-template-columns:repeat(10, 1fr);grid-gap:20px 20px;gap:20px 20px;padding:20px;border:2px solid black}@media (max-width: 1000px){.Board{grid-template-columns:repeat(8, 1fr)}}@media (max-width: 680px){.Board{grid-template-columns:repeat(4, 1fr)}}.Board__box{box-sizing:border-box;display:flex;justify-content:center;width:40px;height:40px;align-items:center;font-size:24px;border:1px solid blue;cursor:pointer;transition:all 0.2s}.Board__box--crossed{text-decoration:line-through;background-color:#b96060}.Board__box--available{background-color:#49b149}.Board__box--available-big{transform:scale(1.2)}.Board__box--unavailable{background-color:grey}.Board__box--final{background-color:gold}

body{background:url("data:image/svg+xml;base64,")}.App{display:flex;flex-direction:column;align-items:center;padding:40px 40px 0;font-family:'Roboto', sans-serif}.App__rules{max-width:600px}.App__button{display:block;margin:20px auto;padding:10px 30px;font-family:inherit;font-size:16px;background-color:beige;border-radius:20px}.App__game-log{max-width:600px}

